
  
  button {
    position: relative;
    background: #242d3c;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 5px 5px;
    color: white;
    text-transform: uppercase;
    letter-spacing: .1em;
    transition: all .7s ease;
    width: 120px;
    height: 35px;
    &:after {
      position: absolute;
      top: 20%;
      left: 50%;
      background: linear-gradient(45deg, rgb(46, 94, 149),rgb(59, 131, 209),rgb(67, 91, 142));
      width: 15px;
      height: 3px;
      content: '';
      transform: translate(-50%);
      border-radius: 25px 25px;
      filter: blur(20px);
      z-index: -1;
    }
    &:hover {
      border-radius: 1px 1px;
      background:#FFDB58;
      letter-spacing: .3em;
      color: rgb(255, 255, 255);
     
    }
  }