.layout {
  z-index: 1;
  .header {
 
    display: flex;
    align-items: center;
    padding: 20px;
    margin-left: auto;
  }
  .content {
    
    display: flex;
    flex-direction: column;
  }

  .footer {
    text-align: center;
    margin-top: auto;
    margin-bottom: 20px;
    padding: 20px;
  }
  &.rtl {
    .header {
      box-shadow: -1px 1px 4px #9aa0b9;
    }
  }
}
